\n        parentIndex: \"\",\r\n      },\r\n      rules: {\r\n        name: [{ required: true, message: \"请输入权限名称\", trigger: \"blur\" }],\r\n        index: [{ required: true, message: \"请输入权限索引\", trigger: \"blur\" }],\r\n        type: [{ required: true, message: \"请选择权限类型\", trigger: \"blur\" }],\r\n      },\r\n      typeOptions: [\r\n        {\r\n          value: \"MENU\",\r\n          label: \"菜单\",\r\n        },\r\n        {\r\n          value: \"REPORT\",\r\n          label: \"报表\",\r\n        },\r\n      ],\r\n      parentIndexOptions: [],\r\n      pickerOptions0: {\r\n        disabledDate(time) {\r\n          return time.getTime() < Date.now() - 8.64e7;\r\n        },\r\n      },\r\n      dialogVisible: false,\r\n    };\r\n  },\r\n  computed: {\r\n    disabled: function() {\r\n      //                return this.dialogInfo.dialogType == 'edit' ? true : false;\r\n      return false;\r\n    },\r\n  },\r\n  methods: {\r\n    getMenuPermissionsOptions() {\r\n      common.ajax({\r\n        url: \"/web/v1/permission/getMenu\",\r\n        type: \"post\",\r\n        contentType: \"application/json\",\r\n        dataType: \"json\",\r\n        success: res => {\r\n          if (res.status == 0 && res.data) {\r\n            let result = res.data;\r\n            this.parentIndexOptions = result.list;\r\n          }\r\n        },\r\n        error: err => {},\r\n      });\r\n    },\r\n    resetForm(formName) {\r\n      if (this.$refs[formName]) {\r\n        this.$refs[formName].resetFields();\r\n      }\r\n    },\r\n\r\n    submit(formName) {\r\n      if (!this.ruleForm.index) {\r\n        this.$message(\"请输入权限索引\");\r\n        return false;\r\n      }\r\n      if (!this.ruleForm.name) {\r\n        this.$message(\"请输入权限名称\");\r\n        return false;\r\n      }\r\n      if (!this.ruleForm.type) {\r\n        this.$message(\"请选择权限类型\");\r\n        return false;\r\n      }\r\n\r\n      this.dialogInfo.disabled = true;\r\n      this.$emit(\"submitForm\", this.dialogInfo.dialogType, this.ruleForm);\r\n    },\r\n\r\n    closeDialog() {\r\n      this.$emit(\"closeDialog\");\r\n    },\r\n  },\r\n  created() {},\r\n  mounted() {\r\n    this.ruleForm = this.dialogInfo.form;\r\n\r\n    //            this.getMenuPermissionsOptions();\r\n  },\r\n  watch: {\r\n    dialogInfo: function(val, oldVal) {\r\n      if (val.dialogType == \"add\") {\r\n        this.resetForm(\"ruleForm\");\r\n      }\r\n      this.getMenuPermissionsOptions();\r\n    },\r\n    id: function(val, oldVal) {\r\n      this.ruleForm = this.dialogInfo.form;\r\n    },\r\n    dialogFormVisible: function(val) {\r\n      this.dialogVisible = !!val;\r\n    },\r\n  },\r\n};\r\n</script>\r\n"]}]);
