exports.push([module.id, "\n.el-table th > .cell[data-v-2a046e1d] {\r\n  text-align: center;\n}\n.create[data-v-2a046e1d] {\r\n  float: right;\r\n  margin-bottom: 20px;\n}\n.mb-10[data-v-2a046e1d] {\r\n  margin-bottom: 10px;\n}\r\n", "",{"version":3,"sources":["src/views/contents/permission/permissionManage.vue"],"names":[],"mappings":";AAsDA;EACA,kBAAA;AACA;AAEA;EACA,YAAA;EACA,mBAAA;AACA;AAEA;EACA,mBAAA;AACA","file":"permissionManage.vue?vue&type=style&index=0&id=2a046e1d&scoped=true&lang=css&","sourcesContent":["<!--\r\n * @Description: \r\n * @Author: 焦丙乾(jiaobingqian)\r\n * @Email: jiaobq123@163.com\r\n * @Date: 2019-07-15 18:33:54\r\n * @LastEditTime: 2019-07-18 17:16:44\r\n * @LastEditors: 靳肖健\r\n -->\r\n<template>\r\n  <tab-page ref=\"tabPage\">\r\n    <template slot=\"permissionManage\">\r\n      <data-table :data-table=\"dataTable\" ref=\"dataTable\">\r\n        <div slot=\"conditions\" style=\"margin-bottom:15px\">\r\n          <el-button\r\n            type=\"primary\"\r\n            class=\"create\"\r\n            size=\"small\"\r\n            @click=\"add()\"\r\n          >&nbsp;&nbsp;新建&nbsp;&nbsp;</el-button>\r\n        </div>\r\n        <template slot=\"columns\">\r\n          <el-table-column prop=\"name\" label=\"权限名称\" align=\"left\" width></el-table-column>\r\n          <el-table-column prop=\"index\" label=\"权限索引\" align=\"left\" width></el-table-column>\r\n          <el-table-column label=\"类型\" align=\"center\">\r\n            <template slot-scope=\"scope\">\r\n              <el-tag :type=\"scope.row.type | typeFilter\">{{scope.row.type==\"MENU\"?'菜单':'报表'}}</el-tag>\r\n            </template>\r\n          </el-table-column>\r\n          <el-table-column prop=\"parent_index\" label=\"父菜单\" align=\"left\" width></el-table-column>\r\n          <el-table-column label=\"状态\" width=\"90\">\r\n            <template slot-scope=\"scope\">\r\n              <el-tag :type=\"scope.row.flag | flagFilter\">{{scope.row.flag == 0?'使用中':'已失效'}}</el-tag>\r\n            </template>\r\n          </el-table-column>\r\n          <el-table-column label=\"操作\" align=\"center\" width>\r\n            <template slot-scope=\"scope\">\r\n              <el-button type=\"text\" size=\"small\" @click=\"edit(scope.row)\">编辑</el-button>\r\n              <el-button type=\"text\" size=\"small\" @click=\"destroy(scope.row)\">删除</el-button>\r\n            </template>\r\n          </el-table-column>\r\n        </template>\r\n      </data-table>\r\n      <report-permission\r\n        ref=\"ruleForm\"\r\n        :dialogInfo=\"dialogInfos\"\r\n        :dialogFormVisible=\"showDialog\"\r\n        :id=\"dialogId\"\r\n        @submitForm=\"submitForm\"\r\n        v-on:closeDialog=\"closeDetailDialog\"\r\n      ></report-permission>\r\n    </template>\r\n  </tab-page>\r\n</template>\r\n<style scoped>\r\n.el-table th > .cell {\r\n  text-align: center;\r\n}\r\n\r\n.create {\r\n  float: right;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.mb-10 {\r\n  margin-bottom: 10px;\r\n}\r\n</style>\r\n<script>\r\nimport Common from \"../../../utils/common\";\r\nimport DataTable from \"../../../components/DataTable\";\r\nimport TabPage from \"../../../components/TabPage\";\r\nimport ReportPermission from \"../permission/permissionDialog.vue\";\r\nexport default {\r\n  components: {\r\n    DataTable,\r\n    TabPage,\r\n    ReportPermission,\r\n    Common,\r\n  },\r\n  data() {\r\n    return {\r\n      showDialog: false,\r\n      dataTable: {\r\n        pageSize: 1000,\r\n        pageSizes: [10, 20, 50, 100, 1000],\r\n        url: \"/web/v1/permission/getAll\",\r\n        restful: true,\r\n        queryParams: {},\r\n        emptyText: \"没有记录\",\r\n      },\r\n      dialogInfos: {\r\n        dialogType: \"add\",\r\n        form: {\r\n          id: \"\",\r\n          name: \"\",\r\n          index: \"\",\r\n          type: \"\",\r\n          parenIndex: \"\",\r\n          level: \"\",\r\n          description: \"\",\r\n        },\r\n        disabled: false,\r\n      },\r\n      dialogId: null,\r\n    };\r\n  },\r\n  filters: {\r\n    typeFilter(item) {\r\n      const itemsMap = {\r\n        MENU: \"success\",\r\n        REPORT: \"primary\",\r\n      };\r\n      return itemsMap[item];\r\n    },\r\n    flagFilter(item) {\r\n      const itemsMap = {\r\n        0: \"success\",\r\n        1: \"error\",\r\n      };\r\n      return itemsMap[item];\r\n    },\r\n  },\r\n  mounted() {\r\n    var defaultPage = {\r\n      label: \"权限管理\",\r\n      name: \"permissionManage\",\r\n      slotName: \"permissionManage\",\r\n      closable: false,\r\n    };\r\n    this.$refs.tabPage.$emit(\"open-tab\", defaultPage);\r\n  },\r\n  methods: {\r\n    loadLists() {\r\n      this.$refs.dataTable.$emit(\"load-data\");\r\n    },\r\n\r\n    enableSubmit() {\r\n      this.dialogInfos.disabled = false;\r\n    },\r\n    add() {\r\n      this.openDialog();\r\n    },\r\n    edit(row) {\r\n      this.openDialog(row);\r\n    },\r\n    destroy(row) {\r\n      let self = this;\r\n      this.$confirm(\"是否确认删除该权限?\", \"提示\", {\r\n        confirmButtonText: \"确认\",\r\n        cancelButtonText: \"取消\",\r\n        type: \"warning\",\r\n      })\r\n        .then(() => {\r\n          Common.ajax({\r\n            url: \"/web/v1/permission/delete/\" + row.id,\r\n            type: \"post\",\r\n            data: {},\r\n            restful: true,\r\n            contentType: \"application/json\",\r\n            success(response) {\r\n              self.$alert(\"删除成功\", \"\", {\r\n                type: \"success\",\r\n                confirmButtonText: \"知道了\",\r\n                callback: action => {\r\n                  self.$refs.dataTable.$emit(\"load-data\");\r\n                  self.$refs.history && this.$refs.history.$emit(\"load-data\");\r\n                },\r\n              });\r\n            },\r\n            error(response) {\r\n              this.$message({\r\n                message: response.message,\r\n                type: \"warning\",\r\n              });\r\n            },\r\n          });\r\n        })\r\n        .catch(e => {\r\n          // console.log(e);\r\n        });\r\n    },\r\n    closeDetailDialog() {\r\n      this.showDialog = false;\r\n    },\r\n    openDialog(row) {\r\n      if (!row) {\r\n        row = {};\r\n      }\r\n\r\n      var dialogType = row.id ? \"edit\" : \"add\";\r\n      this.dialogInfos = {\r\n        dialogType: dialogType,\r\n        form: {\r\n          id: row.id || \"\",\r\n          name: row.name || \"\",\r\n          index: row.index || \"\",\r\n          type: row.type || \"\",\r\n          parentIndex: row.parent_index || \"\",\r\n          level: row.level || \"\",\r\n          description: row.description || \"\",\r\n        },\r\n        disabled: false,\r\n      };\r\n      this.dialogId = row.id || \"\";\r\n      this.showDialog = true;\r\n    },\r\n    submitForm(...args) {\r\n      let successMessage = \"\";\r\n      let { id, name, index, type, description, parentIndex } = args[1];\r\n\r\n      let requestType = args[0];\r\n      var optionType = \"post\";\r\n      var optionUrl = \"/web/v1/permission\";\r\n      var successText = \"添加权限成功\";\r\n      if (requestType == \"edit\") {\r\n        optionUrl = optionUrl + \"/\" + id;\r\n        successText = \"更新权限成功\";\r\n      }\r\n      if (name && index && type) {\r\n        Common.ajax({\r\n          url: optionUrl,\r\n          type: optionType,\r\n          data: {\r\n            name,\r\n            index,\r\n            type,\r\n            description,\r\n            parentIndex,\r\n          },\r\n          restful: true,\r\n          contentType: \"application/json\",\r\n          dataType: \"json\",\r\n          success: res => {\r\n            this.alertMessage = successMessage;\r\n            this.$alert(successText, \"\", {\r\n              type: \"success\",\r\n              confirmButtonText: \"知道了\",\r\n              callback: action => {\r\n                this.enableSubmit();\r\n                this.showDialog = false;\r\n              },\r\n            });\r\n          },\r\n          error: err => {\r\n            let error = err.message || err;\r\n            this.alertMessage = error;\r\n            err.message &&\r\n              this.$alert(this.alertMessage, \"\", {\r\n                type: \"error\",\r\n                confirmButtonText: \"知道了\",\r\n                callback: action => {\r\n                  this.enableSubmit();\r\n                },\r\n              });\r\n          },\r\n          complete: () => {\r\n            this.$refs.dataTable.$emit(\"load-data\");\r\n            this.$refs.history && this.$refs.history.$emit(\"load-data\");\r\n          },\r\n        });\r\n      } else {\r\n        this.$message({\r\n          showClose: true,\r\n          message: \"必填字段不能为空!\",\r\n          type: \"error\",\r\n          duration: 3000,\r\n        });\r\n        this.enableSubmit();\r\n      }\r\n    },\r\n  },\r\n};\r\n</script>\r\n"]}]);
