<template>
  <el-dialog
    :title="dialogInfo.dialogType =='add'?'创建权限':'修改权限'"
    width="50%"
    class="dialog-container"
    v-model="dialogVisible"
    :show-close="true"
    @close="closeDialog"
    :close-on-click-modal="false"
  >
    <div class="dialog-form">
      <el-form class="formInputWidth" :rules="rules" ref="ruleForm" :model="ruleForm">
        <el-form-item label="权限索引" prop="index" :label-width="formLabelWidth">
          <el-input v-model="ruleForm.index" auto-complete="off" :disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item label="权限名称" prop="name" :label-width="formLabelWidth">
          <el-input v-model="ruleForm.name" auto-complete="off" :disabled="disabled"></el-input>
        </el-form-item>
        <!--<el-form-item label="权限级别" :label-width="formLabelWidth">-->
        <!--<el-input v-model="ruleForm.level" auto-complete="off" :disabled="disabled"></el-input>-->
        <!--</el-form-item>-->
        <el-form-item label="权限类型" prop="type" :label-width="formLabelWidth">
          <el-select
            ref="limitSelect"
            v-model="ruleForm.type"
            :disabled="disabled"
            placeholder="请选择"
          >
            <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="权限父菜单" prop="parentIndex" :label-width="formLabelWidth">
          <el-select
            ref="limitSelect"
            clearable
            v-model="ruleForm.parentIndex"
            :disabled="disabled"
            placeholder="请选择"
          >
            <el-option
              v-for="item in parentIndexOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="权限描述" prop="description" :label-width="formLabelWidth">
          <el-input
            v-model="ruleForm.description"
            auto-complete="off"
            autosize
            type="textarea"
            :disabled="disabled"
          ></el-input>
        </el-form-item>
      </el-form>
      <div v-if="!disabled" slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">重置</el-button>
        <span class="wd">&nbsp;</span>
        <el-button type="primary" @click="submit('ruleForm')" :disabled="dialogInfo.disabled">确认</el-button>
      </div>
    </div>
  </el-dialog>
</template>

<style scoped>
.el-dialog__wrapper .el-dialog--customer .el-dialog__header {
  text-align: center;
}

.dialog-form {
  width: 380px;
}

.el-select {
  width: 200px;
}

.el-input {
  width: 200px;
}
.el-input {
  width: 200px;
}

.el-select .el-input {
  width: 260px;
}

.el-select-dropdown__item {
  max-width: 500px;
}

.dialog-footer {
  text-align: center;
}

.el-upload ul {
  display: none;
}

.download-btn {
  text-align: center;
  display: inline-block;
  font-size: 12px;
  border-radius: 5px;
  line-height: 30px;
  padding: 0 8px;
  outline: none;
  color: #ffffff;
  background-color: #20a0ff;
  box-sizing: border-box;
}

.wd {
  display: inline-block;
  width: 50px;
}

.self-line-height {
  line-height: 36px;
}

.self-line-height2 {
  line-height: 48px;
}

.label-wrapper {
  width: 100px;
}

.self-label {
  text-align: right;
  padding-right: 20px;
}

.el-row {
  height: 50px;
  line-height: 50px;
}

.el-form-item__label {
  width: 200px;
}

.text {
  font-size: 14px;
}

.item {
  padding: 10px 20px;
  margin: 10px 0;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

.box-card {
  width: 300px;
  margin-right: 20px;
}

.txt-center {
  text-align: center;
}

.jc-between {
  justify-content: space-between;
}

.mt-30 {
  margin-top: 50px;
}

.left {
  float: right;
}

.form-content-width {
  width: 420px;
}

.mb-30 {
  margin-bottom: 22px;
}
</style>
<style>
.el-upload__files {
  display: none;
}

.content-wrapper {
  display: flex;
  display: -webkit-flex;
  display: -ms-flex;
  margin: 0;
}
</style>

<script>
import common from "../../../utils/common";
export default {
  components: {},
  props: ["dialogInfo", "dialogFormVisible", "id"],
  data() {
    return {
      formLabelWidth: "100px",
      ruleForm: {
        id: "",
        name: "",
        index: "",
        type: "",
        description: "",
        parentIndex: "",
      },
      rules: {
        name: [{ required: true, message: "请输入权限名称", trigger: "blur" }],
        index: [{ required: true, message: "请输入权限索引", trigger: "blur" }],
        type: [{ required: true, message: "请选择权限类型", trigger: "blur" }],
      },
      typeOptions: [
        {
          value: "MENU",
          label: "菜单",
        },
        {
          value: "REPORT",
          label: "报表",
        },
      ],
      parentIndexOptions: [],
      pickerOptions0: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        },
      },
      dialogVisible: false,
    };
  },
  computed: {
    disabled: function() {
      //                return this.dialogInfo.dialogType == 'edit' ? true : false;
      return false;
    },
  },
  methods: {
    getMenuPermissionsOptions() {
      common.ajax({
        url: "/web/v1/permission/getMenu",
        type: "post",
        contentType: "application/json",
        dataType: "json",
        success: res => {
          if (res.status == 0 && res.data) {
            let result = res.data;
            this.parentIndexOptions = result.list;
          }
        },
        error: err => {},
      });
    },
    resetForm(formName) {
      if (this.$refs[formName]) {
        this.$refs[formName].resetFields();
      }
    },

    submit(formName) {
      if (!this.ruleForm.index) {
        this.$message("请输入权限索引");
        return false;
      }
      if (!this.ruleForm.name) {
        this.$message("请输入权限名称");
        return false;
      }
      if (!this.ruleForm.type) {
        this.$message("请选择权限类型");
        return false;
      }

      this.dialogInfo.disabled = true;
      this.$emit("submitForm", this.dialogInfo.dialogType, this.ruleForm);
    },

    closeDialog() {
      this.$emit("closeDialog");
    },
  },
  created() {},
  mounted() {
    this.ruleForm = this.dialogInfo.form;

    //            this.getMenuPermissionsOptions();
  },
  watch: {
    dialogInfo: function(val, oldVal) {
      if (val.dialogType == "add") {
        this.resetForm("ruleForm");
      }
      this.getMenuPermissionsOptions();
    },
    id: function(val, oldVal) {
      this.ruleForm = this.dialogInfo.form;
    },
    dialogFormVisible: function(val) {
      this.dialogVisible = !!val;
    },
  },
};
</script>
